import React, {useState} from 'react'
import {Carousel, Typography} from "antd";

import JPEX from "../assets/img.png";
import Gs1 from "../assets/Gs1.png";
import MyProfile from "../assets/myprofile.png";
import GameMa from "../assets/gamema.png";
import DiscountMate from "../assets/dm.png"

import {ReactComponent as Dotpoint} from "../assets/dotpoint.svg";

import './ProjectShow.css';

const {Title, Text, Paragraph} = Typography


const ProjectShow = ({ProjectList, selectedItem}) => {
    const [showImg, setShowImg] = useState(1)

    const onClickHandler = (id) => {
        setShowImg(id)
        selectedItem(showImg)

    }

    const renderImg = () => {
        const item = ProjectList.find(ele => ele.id === showImg)
        return (
            <div>
                <Title style={{color:'#5A5560'}}>{item.title}</Title>
                <img src={item.img}/>
            </div>


        )
    }

    return (
        <div className="item-layout">
            {renderImg(showImg)}

            <div className="dot-list">
                {ProjectList.map(ele => {
                    return (
                        <Dotpoint
                            className={showImg === ele.id ? 'dot_active' : 'dot'}
                            onClick={() => onClickHandler(ele.id)}/>
                    )
                })}
            </div>


        </div>
    )
}

export default ProjectShow
